.container {
  padding: 0 1rem;
}

a {
  all: unset !important;
  color: inherit;
}
.header {
  display: flex;
  background: #eee;
  align-items: center; }

ul,
li {
  all: unset; }

.header__navigation {
  flex: 2 1; }

.header__logo > img {
  width: 100%;
  height: 30%;
  max-width: 100px; }

.header__list {
  display: flex;
  padding: 0.7rem;
  justify-content: center;
  align-items: center;
  font-size: 1.3rem;
  color: #424242; }

.header__item {
  padding: 0.5rem 1rem;
  cursor: pointer; }
  .header__item:hover {
    background: #464641;
    color: #fff;
    border-radius: 5px; }

.home-block .home__title {
  text-align: center;
  font-size: 2rem;
  color: #424242; }

.home-block .home__intro {
  text-align: center;
  font-size: 1.2rem;
  color: #424242; }

table {
  border-collapse: collapse;
  width: 100%; }

table,
th,
td {
  border: 1px solid #424242ad;
  font-size: 1.3rem; }

thead {
  background: #05cf05;
  color: #eee; }
  thead th {
    height: 31px;
    font-size: 1.5rem; }

tbody tr {
  cursor: pointer; }

tbody tr:hover {
  background: #eee; }

.country-item {
  display: flex;
  align-content: center;
  align-items: center;
  justify-content: center; }

.country-header {
  display: flex;
  margin: 1rem 0.5rem;
  flex-direction: column;
  text-align: center; }
  .country-header .country-flag {
    display: flex;
    width: 100%;
    height: 100%;
    justify-content: center; }
  .country-header img {
    width: 100%;
    height: 100%;
    object-fit: contain;
    box-shadow: 0 0 2px 2px #a7a2a2; }

.country-details {
  display: flex;
  flex-direction: column;
  font-size: 1.5rem;
  flex-basis: 55%;
  margin-left: 4.6rem; }
  .country-details > li {
    margin: 0.6rem 0;
    text-align: left; }
  .country-details .country-geo {
    display: flex;
    flex-direction: column; }
    .country-details .country-geo > ul > li {
      font-size: 1.3rem; }
  .country-details .country__sublist {
    display: flex;
    flex-direction: column;
    margin-left: 0.8rem;
    margin-top: 0.5rem; }

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

